import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { PsFieldEntityComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-entity/ps-field-entity.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsContainerItem, IOptionsPsKeyinInput } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsInputPasscodeExposed } from './ps-input-passcode.component.interfaces';

@Component({
  selector: 'ps-input-passcode',
  templateUrl: './ps-input-passcode.component.html',
  styleUrls: ['./ps-input-passcode.component.scss'],
})
export class PsInputPasscodeComponent extends PsFieldEntityComponent implements OnInit {

  @Input() public options: IOptionsPsInputPasscodeExposed;
  @Output() onPsKeyUp = new EventEmitter<void>();


  public defaultOptions: IOptionsPsKeyinInput = {
    type: 'number',
    mask: {
      regex: '^[0-9]$'
    }
  };

  passcodeItemOptions: IOptionsPsContainerItem = {
    psClass: 'ps-input-passcode'
  };

  codeDigitsOptions = [];

  constructor(public common: PsCommonService, public logger: LoggerService) {
    super(common, logger);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.defaultOptions, this.options, false);
    for (let index = 0; index < 4; index++) {
      const digit: IOptionsPsKeyinInput = {
        fcName: this.defaultOptions.fcName + index,
        group: this.defaultOptions.group,
        mask: {
          regex: this.defaultOptions.mask.regex
        }
      }
      this.codeDigitsOptions.push(digit);
    }

    this.commonProv.setFormData(this.entityFormGroup, this.options.requestObject);
  }

  inputChanged(event) {
    if (event) {
      let inputValue: string = '';
      for (const digit of this.codeDigitsOptions) {
        if (this.options.group.controls[digit.fcName]?.value) {
          inputValue += this.options.group.controls[digit.fcName]?.value;
        }
      }
      this.setEntityValue(parseInt(inputValue))
    }
  }

  moveToNextDigit(field) {

    this.onPsKeyUp.emit(field);

  }
}
