import { Component, Input, OnInit } from '@angular/core';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsSelectDropdownComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-select/ps-select-dropdown/ps-select-dropdown.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IchangeValues, IOptionsPsBaseFieldExposed, IOptionsPsSelectDropdown, IPsSelect } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';

@Component({
  selector: 'ps-dropdown-fund-sources',
  templateUrl: './ps-dropdown-fund-sources.component.html',
  styleUrls: ['./ps-dropdown-fund-sources.component.scss'],
})
export class PsDropdownFundSourcesComponent extends PsSelectDropdownComponent implements OnInit {
  @Input() options: IOptionsPsBaseFieldExposed = {};
  public defaultOptions: IOptionsPsSelectDropdown = {
    listOfOptions: [],
  };

  constructor(
    commonService: PsCommonService,
    logger: LoggerService,
    private omniPullService?: OmniPullService
  ) {
    super(commonService, logger);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.defaultOptions, this.options, false);
    this.loadFundSources();
  }

  /**
   * populating Facility types fetched from server
   */
  private async loadFundSources() {

    const fundSources = {
      gridModel: [
        {
          briefDesc: "Salary And Wages",
          longDesc: "Salary And Wages",
          code: "248",
        },
        {
          briefDesc: "Bonus And Commission",
          longDesc: "Bonus And Commission",
          code: "268",
        },
        {
          briefDesc: "Business Income",
          longDesc: "Business Income",
          code: "278",
        },
        {
          briefDesc: "Investment Return",
          longDesc: "Investment Return",
          code: "898",
        },
        {
          briefDesc: "Centrelink Payments",
          longDesc: "Centrelink Payments",
          code: "874",
        }]
    }

    if (fundSources && fundSources.gridModel && fundSources.gridModel.length > 0) {
      for (const iterator of fundSources.gridModel) {
        const fundSource: IPsSelect = {
          itemValue: iterator.code,
          description: iterator.briefDesc,
          selectedObj: iterator
        };
        this.defaultOptions.listOfOptions.push(fundSource);
      }
    }
  }

  selectDropDownChange(values: IchangeValues) {
    this.onPsChange.emit(values);
  }



}
